<template>
  <div style="width: 100%" class="mt-1">
    <hr
      class="m-2"
      v-if="next ? next.ownerId != c.ownerId || c.createdTime - next.createdTime > 300000: false"
    >
    <div class="card text-warning p-3" v-if="userInfo.blockedUser.includes(c.ownerId)">
      <code>1 blocked message</code>
    </div>

    <div v-else class="message" style="    margin-right: 2rem">
      <div>
        <div
          style="background-size: cover;background-position: center; height: 40px;width:40px;margin-bottom: -40px"
          :style="'background-image: url(' + c.userImg + ');'"
          v-if="next ? next.ownerId != c.ownerId || c.createdTime - next.createdTime > 300000: true"
        ></div>
        
      </div>

      <div class="ml-5">
        <div
          class="text-info"
          v-if="next ? next.ownerId != c.ownerId || c.createdTime - next.createdTime > 300000: true"
        >
          {{c.owner}}
          <span class="small text-secondary">{{calendarTime(c.createdTime)}}</span>
        </div>
      </div>

      <div class="chat-wrapper my-1">
          <p class="text d-inline" v-if="c.message" v-emoji:message="c.message"></p>

          <div v-if="c.ownerId == userInfo._id && c.message" class="float-right">
            <i
              class="fas fa-ellipsis-v btn px-1 py-0 my-0"
              style="cursor:pointer;display:none"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            ></i>
            <div class="dropdown-menu dropdown-menu-right small">
              <button class="dropdown-item" type="button">Edit</button>
              <button class="dropdown-item" type="button" @click="deleteMessage(c)">Delete</button>
            </div>
          </div>
        
        <div v-if="!isMobile">
          <div v-if="c.ownerId == userInfo._id && c.img.length && !c.message" class="float-right">
            <i
              class="fas fa-ellipsis-v btn px-1 py-0 my-0"
              style="cursor:pointer;display:none"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            ></i>
            <div class="dropdown-menu dropdown-menu-right small">
              <button class="dropdown-item" type="button">Edit</button>
              <button class="dropdown-item" type="button" @click="deleteMessage(c)">Delete</button>
            </div>
          </div>
          <img
            :src="img"
            alt
            class="img m-2 rounded"
            :class="{'mobile-size': isMobile}"
            v-for="(img, i) in c.img"
            :key="i"
            @click="show(img, c.img)"
          >
        </div>

        <div v-if="isMobile && c.img.length" class="mb-1 image-thumb">
          <div v-if="c.ownerId == userInfo._id && c.img.length && !c.message" class="float-right m-0">
            <i
              class="fas fa-ellipsis-v btn px-1 py-0m-0" 
              style="cursor:pointer;display:none"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            ></i>
            <div class="dropdown-menu dropdown-menu-right small">
              <button class="dropdown-item" type="button">Edit</button>
              <button class="dropdown-item" type="button" @click="deleteMessage(c)">Delete</button>
            </div>
          </div>

          <ul class="list-group">
            <!-- <a :href="img" target="_blank">{{img.split('/')[7].split('?')[0]}}</a> -->
              <a class="list-group-item p-2" v-for="(img, i) in c.img" :key="i">
                <img :src="img" height='auto' width="50px" @click="show(img, c.img)"/>
                <a :href="img">Image</a>
              </a>
          </ul>
          
        </div>
        <!-- <div
          class="img m-2 rounded"
          v-for="(img, i) in c.img"
          :key="i"
          @click="show(img, c.img)"
          :style="'background-image: url(' + img + ')'"
        ></div>-->
      </div>
    <hr class="mb-0 ml-2" v-if="i == 0">
    </div>

  </div>
</template>

<script>
import { mapState } from "vuex";

import { convertDate, isMobile } from "../utilities/index";
import ImageViewModalVue from "./ImageViewModal";
import db from "../database/firestore";
import query from "../database/db.actions";
import ChatMessage from './ChatMessage'

export default {
  name: "ChatMessage",
  components: {ChatMessage},
  data() {
    return {
      isShow: false,
      lastUserMessageId: "",
      lastMessageCreateTime: 0,
      isMobile: isMobile.any()
    };
  },
  computed: {
    ...mapState({
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      pendingRequests: state => state.auth.pendingRequests
    })
  },
  props: {
    c: Object,
    next: Object,
    i: Number
  },
  methods: {
    calendarTime: convertDate,
    lastMessage: function(ownerId) {
      this.lastUserMessageId = ownerId;
    },
    show(image, images) {
      this.$modal.show(
        ImageViewModalVue,
        { image, images },
        {
          width: this.isMobile ? "100%" : "70%",
          height: "auto",
          style: { "z-index": 9000, "border-radius": "0.125rem !important" },
          classes: "all-modal"
        },
        {
          closed: this.hide
        }
      );
    },
    hide() {
      this.$modal.hide("view-post");
    },
    deleteMessage(m) {
      console.log(m);
      let vm = this;
      this.$modal.show('dialog', {
        title: "DELETE MESSAGE",
        text: `Are you sure you want to delete this message?
        <a  class="list-group-item px-4 py-0" style="border: none;"><div  class="mt-1" style="width: 100%;"><hr class="m-2"> <div class="message" style="margin-right: 2rem;"><div><div style="background-size: cover; background-position: center center; height: 40px; width: 40px; margin-bottom: -40px; background-image: url(${m.userImg});"></div></div> <div class="ml-5"><div class="text-info">
        ${m.owner}
        <span class="small text-secondary">${convertDate(m.createdTime)}</span></div></div> <div class="chat-wrapper my-1"><p class="text d-inline message" style="margin-left: 50px;">${m.message || m.img.length + ' image(s)'}</p></div> <hr class="mb-0 ml-2"></div></div></a>
        `,
        buttons: [{
          title: '<button class="btn">CLOSE</button>'
        },{
          title: '<button class="btn text-danger">DELETE</button>',
          handler: function() {
            db.collection('message').doc(m._id).delete().then(function() {
                console.log("Document successfully deleted!");
                var options =  {
                  content: "<span class='text-success text-center'>Message deleted</span>", 
                  style: "snackbar", // add a custom class to your snackbar
                  timeout: 3000 // time in milliseconds after the snackbar autohides, 0 is disabled
                }
                $.snackbar(options);
                vm.$modal.hide('dialog');
            }).catch(function(error) {
                console.error("Error removing document: ", error);
            });;
          }
        }]
      });
    }
  }
};
</script>


<style scoped>
.message:hover i, .image-thumb:hover i {
  display: block !important;
}
.image-thumb p:nth-child(2) {
  margin-top: 0 !important;
}
.image-thumb p:last-child {
  margin-bottom: 0 !important;
}
#thumbnailImage {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.activeimg {
  border: 2px solid;
  filter: blur(0px) !important;
}
.img {
  height: auto;
  max-height: 300px;
  width: auto;
  background-repeat: no-repeat;
  background-size: cover;
  cursor: pointer;
}
.mobile-size {
  max-width: 70vw !important;
}
.chat-wrapper {
  margin-left: 60px;
  width: 100%;
}
.text {
  width: 100%;
  padding-right: 50px;
}
.text-info {
  margin-left: 10px;
  margin-bottom: 5px;
}
.user-image {
  height: 30px;
  width: 30px;
}
hr {
  width: 100%;
}
p {
  margin: 5px;
}
</style>



