<template>
  <div id="listChat" class="chat-list col-2 bg-white p-0" :style="`height: ${wh};overflow-y: auto`" :class="{'hide-menu col-12': isMobile}">
    <div class="container-fluid p-0">
      <nav class="navbar navbar-expand-md navbar-dark bg-dark">
        <i class="fas fa-chevron-left fa-lg text-white" @click="openMenu()" v-if="isMobile"></i>
        <a class="navbar-brand text-info dropdown-toggle mr-auto"  v-if="getUsername" role="button" id="dropdownMenuLink" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          &nbsp; {{getUsername}}
        </a>
        <router-link class="navbar-brand text-info" v-else to="/login">Login</router-link>
        <div class="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuLink">
          <router-link  class="dropdown-item" to="/profile">Profile</router-link>
          <a  class="dropdown-item" style="cursor:pointer" @click="signOut()">Logout</a>
        </div>
      </nav>
      <div id="accordion">
        <div class="card" style="border: none !important">
          <router-link
            class="card-header"
            data-toggle="collapse"
            data-target="#collapseOne"
            to="/personal"
            :class="[route == 'personal' ? 'bg-secondary text-white' : 'bg-dark text-secondary']"
          >
            <div class="card-link" data-toggle="collapse">Personal</div>
          </router-link>
          <div id="collapseOne" class="collapse show" data-parent="#accordion">
            <ul class="list-group card-body p-0">
              <router-link
                :to="'/personal/' + c.groupId"
                class="text-secondary list-group-item d-flex justify-content-between align-items-center"
                style="text-decoration: none"
                v-for="c in chatList"
                :key="c.id"
                :class="{'bg-light': groupId == c.groupId}"
              >
                <div class="bmd-list-group-col m-0" v-if="newMessages.hasOwnProperty(c.groupId)" style="width: 100%">
                  <div class="list-group-item-heading d-flex justify-content-between m-0">
                    <p class="text-dark mr-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{c.membersName[0] == userInfo.name ? c.membersName[1] : c.membersName[0]}}</p>
                    <small class="float-right">{{calendarTime(newMessages[c.groupId][0].createdTime)}}</small>
                  </div>
                  <div class="list-group-item-text d-flex justify-content-between">
                    <div class="pb-1" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{newMessages[c.groupId][0].message || 'Image'}}</div>
                    <span v-if="nMessageCount[c.groupId]" class="badge" :class="[nMessageCount[c.groupId] ? 'badge-danger' : 'badge-secondary']">{{nMessageCount[c.groupId]}}</span>
                  </div>
                </div>
                <div class="bmd-list-group-col m-0" v-else-if="oldMessages.hasOwnProperty(c.groupId)" style="width: 100%">
                  <div class="list-group-item-heading d-flex justify-content-between m-0">
                    <p class="text-dark mr-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{c.membersName[0] == userInfo.name ? c.membersName[1] : c.membersName[0]}}</p>
                    <small class="float-right">{{calendarTime(oldMessages[c.groupId][0].createdTime)}}</small>
                  </div>
                  <div class="list-group-item-text d-flex justify-content-between">
                    <div class="pb-1" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{oldMessages[c.groupId][0].message || 'Image'}}</div>
                    <span v-if="nMessageCount[c.groupId]" class="badge" :class="[nMessageCount[c.groupId] ? 'badge-danger' : 'badge-secondary']">{{nMessageCount[c.groupId]}}</span>
                  </div>
                </div>
                <div v-else class="text-dark">{{c.membersName[0] == userInfo.name ? c.membersName[1] : c.membersName[0]}}</div>
              </router-link>
            </ul>
          </div>
        </div>
        <!-- group -->
        <div class="card" style="border: none !important">
          <router-link
            class="card-header"
            data-toggle="collapse"
            data-target="#collapseTwo"
            to="/group"
            :class="[route == 'groups' ? 'bg-secondary text-white' : 'bg-dark text-secondary']"
          >
            <div class="collapsed card-link">Group</div>
          </router-link>
          <div id="collapseTwo" class="collapse" data-parent="#accordion">
            <ul class="list-group card-body p-0">
              <a v-for="c in joinedGroupList" :key="c.groupId">
                <router-link
                  :to="'/group/' + c.groupId"
                  class="text-secondary list-group-item d-flex justify-content-between align-items-center"
                  style="text-decoration: none"
                  :class="{'bg-light': groupId == c.groupId}"
                >
                  <div class="bmd-list-group-col m-0" v-if="newMessages.hasOwnProperty(c.groupId)" style="width: 100%">
                    <div class="list-group-item-heading d-flex justify-content-between m-0">
                      <p class="text-dark mr-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{c.groupName}}</p>
                      <small class="float-right">{{calendarTime(newMessages[c.groupId][0].createdTime)}}</small>
                    </div>
                    <div class="list-group-item-text d-flex justify-content-between">
                      <div class="pb-1" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{newMessages[c.groupId][0].owner}}: {{newMessages[c.groupId][0].message || 'Image'}}</div>
                      <span v-if="nMessageCount[c.groupId]" class="badge" :class="[nMessageCount[c.groupId] ? 'badge-danger' : 'badge-secondary']">{{nMessageCount[c.groupId]}}</span>
                    </div>
                  </div>
                  <div class="bmd-list-group-col m-0" v-else-if="oldMessages.hasOwnProperty(c.groupId)" style="width: 100%">
                    <div class="list-group-item-heading d-flex justify-content-between m-0">
                      <p class="text-dark mr-0" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{c.groupName}}</p>
                      <small class="float-right">{{calendarTime(oldMessages[c.groupId][0].createdTime)}}</small>
                    </div>
                    <div class="list-group-item-text d-flex justify-content-between">
                      <div class="pb-1" style="overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{oldMessages[c.groupId][0].owner}}: {{oldMessages[c.groupId][0].message || 'Image'}}</div>
                      <span v-if="nMessageCount[c.groupId]" class="badge" :class="[nMessageCount[c.groupId] ? 'badge-danger' : 'badge-secondary']">{{nMessageCount[c.groupId]}}</span>
                    </div>
                  </div>
                  <div v-else class="text-dark">{{c.groupName}}</div>
                </router-link>
              </a>
            </ul>
          </div>
        </div>
        <div class="card" style="border: none !important;width: 100%">
          <router-link
            class="card-header"
            data-toggle="collapse"
            data-target="#collapseThree"
            to="/public"
            :class="[route == 'public' ? 'bg-secondary text-white' : 'bg-dark text-secondary']"
          >
            <div class="collapsed card-link">Public</div>
          </router-link>
          <div id="collapseThree" class="collapse" data-parent="#accordion"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { isMobile, swipedetect, calendarTime, convertShortDate, createNotify } from "../utilities";
import moment from "moment";
import db from "../database/firestore";

export default {
  name: "Menu",
  computed: {
    ...mapState({
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      chatList: state => state.chat.chatList,
      pendingRequests: state => state.auth.pendingRequests,
      friendNotify: state => state.auth.friendNotify,
      converList: state => state.chat.converList,
      joinedGroupList: state => state.group.joinedGroupList
    })
  },
  data() {
    return {
      groupId: this.$route.params.id,
      groupChatId: "",
      route: "",
      isMobile: isMobile.any(),
      wh: window.innerHeight + 'px',
      newMessages: {},
      oldMessages: {},
      now: Date.now(),
      isNew: {},
      isUnread: {},
      newCount: 0,
      nMessageCount: {}
    };
  },
  watch: {
    userInfo(n, o) {
      // this.$store.dispatch("getAllUserWithConversation", this.userInfo.id);
      // this.$store.dispatch("getAllJoinedGroup", this.userInfo._id);
    },
    $route(n, o) {
      this.groupId = n.params.id;
      this.route = n.name;
      console.log(this.route)
      if (this.isMobile && (this.groupId || this.route == 'public' || this.route == "profile")) {
        document.getElementById("listChat").style.left = "-100%";
        document.getElementById("menu-overlay").classList.remove('menu-overlay');
      }
      if (this.groupId && this.route == "group") {
        if (!$.isEmptyObject(this.newMessages)) {
          window.localStorage.setItem(this.groupId, this.newMessages[this.groupId][0].createdTime);
        } else if (!$.isEmptyObject(this.oldMessages)) {
          window.localStorage.setItem(this.groupId, this.oldMessages[this.groupId][0].createdTime);
        }
        this.$set(this.nMessageCount, this.groupId, 0);
      }
      if (this.groupId && this.route == "personal") {
        if (!$.isEmptyObject(this.newMessages)) {
          window.localStorage.setItem(this.groupId, this.newMessages[this.groupId][0].createdTime);
        } else if (!$.isEmptyObject(this.oldMessages)) {
          window.localStorage.setItem(this.groupId, this.oldMessages[this.groupId][0].createdTime);
        }
        this.$set(this.nMessageCount, this.groupId, 0);
      }
    },
    chatList(n) {
      // this.$store.dispatch("getAllUserWithConversation", this.userInfo.id);
      for (let c of n) {
        this.getOldMessage(c.groupId);
        this.getNewMessage(c.groupId, c.groupName);
        this.countNewMessage(c.groupId);
        // this.countUnreadMessage(c.groupId);
      }
    },  
    joinedGroupList(n, o) {
      if (this.$route.name == "group" && n.length != o.length) {
        this.$router.push("/group/" + n[n.length - 1].groupId);
      }
      for (let g of n) {
        this.getOldMessage(g.groupId);
        this.getNewMessage(g.groupId, g.groupName);
        this.countNewMessage(g.groupId);
        // this.countUnreadMessage(g.groupId);
      }
    },
    newMessages(n, o) {
      console.log(n)
      // for (let m of n) {
        
      // }
    },
    newCount(n) {
      document.title = `(${n}) ${document.title.substring(4)}`
    },
    nMessageCount(n) {
      console.log(n)
      this.$forceUpdate();
    }
  },
  beforeMount() {
    // this.$store.dispatch("getAllUserWithConversation", this.userInfo.id);
  },
  mounted() {
    $("#listChat").bootstrapMaterialDesign();
    swipedetect(document.getElementById('listChat'), dir => {
      if (dir == 'left') {
        document.getElementById('listChat').style.left = "-100%";
        document.getElementById("menu-overlay").classList.remove('menu-overlay');
      }
    })
    let vm = this;
    window.onfocus = function() {
      if (vm.groupId && vm.route == "group") {
        if (!$.isEmptyObject(vm.newMessages)) {
          window.localStorage.setItem(vm.groupId, vm.newMessages[vm.groupId][0].createdTime);
        } else if (!$.isEmptyObject(vm.oldMessages)) {
          window.localStorage.setItem(vm.groupId, vm.oldMessages[vm.groupId][0].createdTime);
        }
        vm.$set(vm.nMessageCount, vm.groupId, 0);
      }
      if (vm.groupId && vm.route == "personal") {
        if (!$.isEmptyObject(vm.newMessages)) {
          window.localStorage.setItem(vm.groupId, vm.newMessages[vm.groupId][0].createdTime);
        } else if (!$.isEmptyObject(vm.oldMessages)) {
          window.localStorage.setItem(vm.groupId, vm.oldMessages[vm.groupId][0].createdTime);
        }
        vm.$set(vm.nMessageCount, vm.groupId, 0);
      }
    }
    // console.log(this.chatList, this.joinedGroupList)
    // for (let c of this.chatList) {
    //   this.getNewMessage(c.groupId, c.groupName);
    //   this.getOldMessage(c.groupId);
    //   // this.countNewMessage(c.groupId);
    // }
    // for (let g of this.joinedGroupList) {
    //   this.getNewMessage(g.groupId, g.groupName);
    //   this.getOldMessage(g.groupId);
    //   // this.countNewMessage(g.groupId);
    // }
  },
  destroyed() {
    window.onfocus = null;
  },
  methods: {
    getLastReadMessage: function(groupId) {
      return window.localStorage.getItem(groupId) || 0;
    },
    countAllNotify: function() {
      let a = 0;
      if (!$.isEmptyObject(this.nMessageCount))
      for (let i in this.nMessageCount) {
        a += this.nMessageCount[i];
      }
      if (a) document.title = `(${a}) HUNRE Chat`;
    },
    calendarTime : isMobile.any() ? convertShortDate : calendarTime,
    countNewMessage: function(groupId) {
      let count = 0, lastReadMessageTime = this.getLastReadMessage(groupId);
      this.$set(this.isNew, groupId, true);

      if (!$.isEmptyObject(this.newMessages))
      for (let i = 0; i < this.newMessages[groupId].length; i++) {
        if (this.newMessages[groupId][i].ownerId != this.userInfo._id && this.isNew[groupId] && this.newMessages[groupId][i].createdTime > lastReadMessageTime) {
          count++;
        } else {
          this.$set(this.isNew, groupId, false);
        }
      }
      this.$set(this.nMessageCount, groupId, count)
      console.log(count)
      return count;
      // if (this.isNew[groupId]) return count;
      // else return 0
    },
    countUnreadMessage: function(groupId) {
      this.countNewMessage(groupId);
      let count = 0, lastReadMessageTime = this.getLastReadMessage(groupId);
      this.$set(this.isUnread, groupId, true);
      if (!$.isEmptyObject(this.oldMessages))
      for (let i = 0; i < this.oldMessages[groupId].length; i++) {
        if (this.oldMessages[groupId][i].ownerId != this.userInfo._id && this.isUnread[groupId] && this.oldMessages[groupId][i].createdTime > lastReadMessageTime) {
          console.log(lastReadMessageTime, this.oldMessages[groupId][i])
          count++;
        } else {
          this.$set(this.isUnread, groupId, false);
        }
      }
      count += this.nMessageCount[groupId];
      this.$set(this.nMessageCount, groupId, count);
      this.$forceUpdate();
    },
    openConversation: function(c) {},
    openMenu: function() {
      if (document.getElementById("listChat").style.left == "-100%") {
        document.getElementById("menu-overlay").classList.add('menu-overlay');
        document.getElementById("listChat").style.left = "0";
      } else {
        document.getElementById("menu-overlay").classList.remove('menu-overlay');
        document.getElementById("listChat").style.left = "-100%";
      }
    },
    signOut() {
      this.$store.dispatch("REMOVE_USER", this.userInfo);
      // let vm = this;
      // setTimeout(() => {
      //   vm.$router.push('/')
      // }, 2000)
    },
    createNotify,
    getNewMessage(groupId, groupName) {
      let vm = this;
      console.log(groupId)
      if (!groupId) return;
      db
        .collection("message")
        .where("groupId", "==", groupId)
        .where("createdTime", ">", this.now)
        .orderBy("createdTime", "desc")
        .onSnapshot(querySnapshot => {
          let a = [];
          console.log(querySnapshot);
          if (querySnapshot.empty) {
            console.log({ empty: true });
            // this.newMessages[groupId] = [];
          } else {
            querySnapshot.forEach(doc => {
              a.push(doc.data());
            });
            console.log(a);
            this.$set(this.newMessages, groupId, a)
            // this.newMessages[groupId] = a;
            
          }
          vm.countUnreadMessage(groupId);
          this.countAllNotify();
          querySnapshot.docChanges().forEach(function(change) {
            if (change.type === "added") {
                vm.$set(vm.isNew, groupId, true);
                vm.newCount++;
                console.log("New: ", change.doc.data());
                let m = change.doc.data();
                if (m.ownerId != vm.userInfo._id && !document.hasFocus()) {
                  if (m.img.length) {
                    vm.createNotify({
                      body: m.message || 'Image',
                      icon: m.userImg || '',
                      image: m.img[0]
                    }, m.owner + ' sends you a messsage in ' + groupName);
                  } else {
                    vm.createNotify({
                      body: m.message,
                      icon: m.userImg || ''
                    }, m.owner + ' sends you a messsage in ' + groupName);
                  }
                }
            }
            if (change.type === "modified") {
                console.log("Modified: ", change.doc.data());
            }
            if (change.type === "removed") {
                console.log("Removed: ", change.doc.data());
            }
          });
        });
    },
    getOldMessage(groupId) {
      console.log(groupId)
      db.collection("message")
        .where("groupId", "==", groupId)
        .where("createdTime", "<", this.now)
        .orderBy("createdTime", "desc")
        .limit(30)
        .onSnapshot(querySnapshot => {
          let a = [];
          console.log(querySnapshot);
          if (querySnapshot.empty) {
            console.log({ empty: true });
            // this.oldMessages = [];
          } else {
            querySnapshot.forEach(doc => {
              a.push(doc.data());
            });
            console.log(a);
            this.$set(this.oldMessages, groupId, a)
            // this.oldMessages = a;
            this.countUnreadMessage(groupId);
            this.countAllNotify();
          }
        });
      // this.isScrollable = true;
    },
  }
};
</script>

<style scoped>
.badge {
  line-height: 1.5;
}
.list-group-item.d-flex.justify-content-between.align-items-center:hover {
  background-color: rgba(0, 0, 0, 0.1);
  cursor: pointer;
}
a {
  text-decoration: none;
}
.bg-secondary.text-white {
  border-left: 7px solid var(--primary);
}
.hide-menu {
  left: -100%;
  position: fixed;
  z-index: 9999;
}
.chat-list {
  transition: all .2s ease-in-out;
}
</style>



