const getFromBetween = {
  results: [],
  string: "",
  getFromBetween: function(sub1, sub2) {
    if (this.string.indexOf(sub1) < 0 || this.string.indexOf(sub2) < 0)
      return !1;
    var SP = this.string.indexOf(sub1) + sub1.length,
      string1 = this.string.substr(0, SP),
      string2 = this.string.substr(SP),
      TP = string1.length + string2.indexOf(sub2);
    return this.string.substring(SP, TP);
  },
  removeFromBetween: function(sub1, sub2) {
    if (this.string.indexOf(sub1) < 0 || this.string.indexOf(sub2) < 0)
      return !1;
    var removal = sub1 + this.getFromBetween(sub1, sub2) + sub2;
    this.string = this.string.replace(removal, "");
  },
  getAllResults: function(sub1, sub2) {
    if (!(this.string.indexOf(sub1) < 0 || this.string.indexOf(sub2) < 0)) {
      var result = this.getFromBetween(sub1, sub2);
      this.results.push(result),
        this.removeFromBetween(sub1, sub2),
        this.string.indexOf(sub1) > -1 &&
          this.string.indexOf(sub2) > -1 &&
          this.getAllResults(sub1, sub2);
    }
  },
  get: function(string, sub1, sub2) {
    if (
      ((this.results = []),
      (this.string = string),
      null == string || "" == string || null == string)
    )
      return string;
    try {
      return this.getAllResults(sub1, sub2), this.results;
    } catch (e) {
      console.log(e);
    }
  }
};
function isEmpty(obj) {
  for (var key in obj) {
    if (obj.hasOwnProperty(key)) return false;
  }
  return true;
}
Vue.directive(EMOJI, {
  bind(el, binding) {
    const ranges = /([\uE000-\uF8FF]|\uD83C[\uDC00-\uDFFF]|\uD83D[\uDC00-\uDFFF]|[\u2694-\u2697]|\uD83E[\uDD10-\uDD5D]|\s)/g;

    const removeEmoji = str => str.replace(new RegExp(ranges, "g"), "");

    const isOnlyEmojis = str => !removeEmoji(str).length;
    let value = binding.value;
    let regex = /:\w{2,}:/g; // shortcode, eg: :smile:
    let regex2 = /(:\w+:|<[/\\]?3|[\\\D|*$][-^]?[:;=]|[:;=B8][-^]?[3DOPp@$*\\)(/|])(?=\s|[!.?]|$)/g; // emoticon :)
    let regex3 = /:\W{1,}/g;
    let a = value.match(regex);
    let b = value.match(regex2);
    let c = value.match(regex3);

    if (a != null) {
      a.forEach(v => {
        emojiIndex.search(v.substr(1, v.length - 2)).map(o => {
          value = value.replace(v, twemoji.convert.fromCodePoint(o.unified));
        });
      });
    }
    if (b != null) {
      b.forEach(v => {
        emojiIndex.search(v.trim()).map(o => {
          value = value.replace(v, twemoji.convert.fromCodePoint(o.unified));
        });
      });
    }
    if (c != null) {
      c.forEach(v => {
        emojiIndex.search(v.trim()).map(o => {
          value = value.replace(v, twemoji.convert.fromCodePoint(o.unified));
        });
      });
    }
    isOnlyEmojis(value) ? "" : el.classList.add("message");
    el.innerHTML = twemoji.parse(value, {
      folder: "svg",
      ext: ".svg"
    });
  },
  update(el, binding) {
    if (el.getAttribute("contenteditable")) console.log(binding);
  }
});

Vue.directive(POST, {
  bind(el, binding) {
    let value = binding.value,
      content = value.content,
      img = value.img;

    if (isEmpty(img)) {
      el.innerHTML = content;
      return;
    } else if (img) {
      let arr = getFromBetween.get(content, "<<I", ">>");
      if (typeof arr == "object")
        for (let a of arr) {
        let i = `<img src="${img[a]}" class="post-image" style="width:100%;">`;
        content = content.replace(`<<I${a}>>`, i);
      }
      el.innerHTML = content;
	  }
  },
  update(el, binding) {
    let value = binding.value,
      content = value.content,
      img = value.img;

    if (isEmpty(img)) {
      el.innerHTML = content;
      return;
    } else if (img) {
      let arr = getFromBetween.get(content, "<<I", ">>");
      if (typeof arr == "object")
        for (let a of arr) {
        let i = `<img src="${img[a]}" class="post-image" style="width:100%;">`;
        content = content.replace(`<<I${a}>>`, i);
      }
      el.innerHTML = content;
	  }
  }
});

Vue.directive(USER, {
  inserted(el) {
    let postId = el.innerText;
    db.collection("users")
      .where("id", "==", postId.trim())
      .get()
      .then(doc => {
        console.log(doc.docs[0].data());
        let user = doc.docs[0].data();
        el.innerText =
          user.name + ` <small class="text-muted">${user.email}</small>`;
      });
  }
});



