<template>
  <div>
    <nav
      class="navbar navbar-expand-md navbar-dark bg-dark fixed-top"
      :class="{' offset-2': !isMobile}"
    >
      <i class="fas fa-chevron-left fa-lg text-white" @click="openMenu()" v-if="isMobile"></i>
      <a
        class="navbar-brand text-white m-0"
        :class="{'nav-mobile': isMobile}"
        style=" text-transform: capitalize;"
      >&nbsp; {{current || $route.name}}</a>
      
      <ul class="nav ml-auto">
        <li class="nav-item" v-if="nav == 'group' && hasId">
          <a class="nav-link" @click="getInviteCode()">
            <i class="fas fa-user-plus text-white"></i>
          </a>
        </li>
        <li class="nav-item" v-if="nav == 'group' && hasId">
          <a class="nav-link" @click="groupSetup()">
            <i class="fas fa-cog text-white"></i>
          </a>
        </li>
        <li class="nav-item"  v-if="nav == 'group' && hasId && isMobile">
          <a class="nav-link" @click="openUserList()">
            <i class="fas fa-user-friends text-white"></i>
          </a>
        </li>
        <li class="nav-item" v-if="nav == 'personal' && hasId">
          <a class="nav-link" @click="leaveConversation()">
            <i class="fas fa-sign-out-alt text-white"></i>
          </a>
        </li>
        <li class="nav-item" v-if="nav == 'personal' && hasId">
          <a class="nav-link" @click="openSetting()">
            <i class="fas fa-cog text-white"></i>
          </a>
        </li>
        <li class="nav-item notify-dropdown">
						<div class="dropdown">
							<a href="javascript:void(0)" class="nav-link text-white" data-toggle="dropdown" @click="notiRead()">
								<i class="fa-bell fa-lg" :class="[notify.length ? 'fas' : 'far']"></i><small class="notify-count border-dark bg-danger text-white rounded-circle" v-if="notify.length">{{ notify.length > 99 ? '99+' : notify.length }}</small>
							</a>
							<div class="dropdown-menu dropdown-menu-right p-0" style="left: -350px;max-width: 100vw">
								<ul class="list-group list-group-flush notify-chat" id="notify">
									<h5 class="dropdown-header" v-if="notify.length > 0">New</h5>
									<li class="list-group-item list-group-item-action list-group-item-light" style="cursor: pointer" v-for="noti in friendNotify" :key="noti.id">
										{{ noti.name }} rejected your friend request.
									</li>
									<li class="list-group-item list-group-item-action list-group-item-light d-flex" style="cursor: pointer" v-for="noti in notify" :key="noti.id" @click="show(noti.postId);">
										<div class="m-0"><img class="px-1 m-0 rounded-circle" style="max-height: 3em;width: 3em;object-fit: contain;" :src="noti.userImg" alt="" /></div>
										<div class="flex-grow-1" style="width: calc(100% - 3em);">
											<span class="text-primary m-0">{{ noti.user }} </span> {{ getType(noti.type) }} <span class="badge badge-light"> {{ cvDate(noti.createdTime) }}</span>
										</div>
									</li>
									<h5 class="dropdown-header">Old</h5>
									<li class="list-group-item list-group-item-action list-group-item-light d-flex" v-for="noti in inactiveNotify" :key="noti.id" @click="show(noti.postId);">
										<div class="m-0"><img class="px-1 m-0 rounded-circle" style="max-height: 3em;width: 3em;object-fit: contain;" :src="noti.userImg" alt="" /></div>
										<div class="text-secondary flex-grow-1" style="width: calc(100% - 3em);">
											<span class="text-primary m-0">{{ noti.user }} </span> {{ getType(noti.type) }} <span class="badge badge-light"> {{ cvDate(noti.createdTime) }}</span>
										</div>
									</li>
								</ul>
							</div>
						</div>
					</li>
      </ul>
      <!-- <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarTogglerDemo02"
        aria-controls="navbarTogglerDemo02"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarTogglerDemo02">
        <form class="form-inline my-2 my-md-0">
          <ul class="navbar-nav mr-auto mt-2 mt-md-0">
            <li class="nav-item active" v-if="nav == 'group' && hasId">
              <a class="nav-link" @click="getInviteCode()">
                <i class="fas fa-user-plus"></i>
              </a>
            </li>
            <li class="nav-item active" v-if="nav == 'group' && hasId">
               <a class="nav-link" @click="groupSetup()">
                <i class="fas fa-cog text-white"></i>
              </a>
            </li>
          </ul>
          <input class="form-control mr-sm-2" type="search" placeholder="Search">
          <button class="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
        </form> 
      </div>-->
    </nav>
  </div>
</template>

<script>
import { mapState } from "vuex";
import GroupSetup from "./GroupSetup";
import PersonalSetup from "./PersonalSetup";
import modal from "./Modal";
import { GET_NOTIFICATION, REMOVE_USER, NOTIFY_READ, GET_POST, GET_INACTIVE_NOTIFICATION, GET_CHAT_LIST, CHAT_NOTIFY_READ, GET_FRIENDS_POST } from "../store/actions.type";
import { convertShortDate, isMobile, calendarTime } from "../utilities";

export default {
  name: "Nav",
  data() {
    return {
      current: "",
      nav: this.$route.name,
      hasId: this.$route.params.id,
      group: {},
      isMobile: isMobile.any() ? true : false,
      title: document.title,
    };
  },
  computed: {
    ...mapState({
      unsub: state => state.postView.unsubPost,
			post: state => state.postView.post,
      userInfo: state => state.auth.userInfo,
      getUserStatus: state => state.auth.userInfo.status,
      getUsername: state => state.auth.userInfo.name,
      notify: state => (state.auth.notify ? state.auth.notify : []),
      inactiveNotify: state =>
        state.auth.inactiveNoti ? state.auth.inactiveNoti : [],
      chatList: state => state.chat.chatList,
      pendingRequests: state => state.auth.pendingRequests,
      friendNotify: state => state.auth.friendNotify,
      converList: state => state.chat.converList,
      joinedGroupList: state => state.group.joinedGroupList
    })
  },
  watch: {
    notify() {
			if (this.notify.length) {
				document.title = "(" + this.notify.length + ")" + this.title;
			}
		},
    $route(n) {
      this.current = '';
      this.nav = n.name;
      this.hasId = n.params.id;
      if (n.name == "personal") {
        try {
          this.group =
            this.chatList.filter(e => e.groupId == n.params.id)[0] || {};
          this.current =
            this.group.membersName[0] != this.userInfo.name
              ? this.group.membersName[0]
              : this.group.membersName[1];
        } catch (e) {}
      }
      if (n.name == "group") {
        this.hasId = n.params.id;
        this.group =
          this.joinedGroupList.filter(e => e.groupId == n.params.id)[0] || {};
        this.current = this.group.groupName;
        console.log(this.joinedGroupList);
      }
      document.title = this.current || 'Home';
    },
    chatList() {
			console.log(this.chatList);
			console.log(12321312);
			let a = 0;
			this.chatList.forEach(chat => {
				if (chat.isNotified != this.userInfo.name && chat.hasNew == true) {
					a++;
					let audio = new Audio("https://facebook.design/public/sounds/Notification%201.mp3");
					audio.preload = "auto";
					setTimeout(() => {
						audio.play();
					}, 250);
				}
			});
      this.count = a;
      if (a != 0 || this.notify.length != 0) {
        document.title = "(" + (a + this.notify.length) + ")" + " " + this.title;
      }
			// a != 0 || this.notify.length != 0 ? (document.title = "(" + (a + this.notify.length) + ")" + " " + this.title) : "";
		}
  },
  methods: {
    cvDate: convertShortDate,
    getInviteCode: function() {
      if (this.nav == "group") {
        let url = this.group.inviteCode;
        console.log(url);
        this.$modal.show("dialog", {
          title: "Invite friend",
          text: `<div class="input-group">
									<input type="text" value="${url}" id="sharedLink" class="form-control" readonly>
									<div class="input-group-append">
										<button class="btn btn-outline-secondary" onclick="(function(){
											document.getElementById('sharedLink').select();
											document.getElementById('alert').classList.remove('text-hide');
											document.execCommand('copy');
										})(); return false;"><i class="fas fa-copy"></i></button>
									</div>
								</div>
								<div class="alert alert-success my-1 p-1 text-center text-hide" id="alert">
									Link has been copied to clipboard
                </div>`,
          buttons: [
            {
              title: "CLOSE"
            }
          ]
        });
      }
    },
    groupSetup: function() {
      console.log(this.group, this.userInfo);
      this.$modal.show(
        GroupSetup,
        { group: this.group, userInfo: this.userInfo },
        {
          height: "auto",
          width: this.isMobile ? "100%" : "50%",
          style: { "z-index": 9000 },
          scrollable: true,
          classes: 'all-modal'
        },
        {
          closed: this.hide
        }
      );
    },
    openSetting: function() {
      console.log(this.hasId, this.group)
      this.$modal.show(
        PersonalSetup,
        { group: this.group, userInfo: this.userInfo, id: this.hasId},
        {
          height: "auto",
          width: this.isMobile ? "100%" : "50%",
          style: { "z-index": 9000 },
          scrollable: true,
          classes: 'all-modal'
        },
        {
          closed: this.hide
        }
      );
    },
    hide() {
      this.$modal.hide("view-post");
      history.pushState(null, "", "/");
    },
    openMenu: function() {
      if (document.getElementById("listChat").style.left == "-100%") {
        document.getElementById("menu-overlay").classList.add("menu-overlay");
        document.getElementById("listChat").style.left = "0";
      } else {
        document
          .getElementById("menu-overlay")
          .classList.remove("menu-overlay");
        document.getElementById("listChat").style.left = "-100%";
      }
    },
    openUserList: function() {
      if (document.getElementById("userList").style.right == "-100%") {
        // document.getElementById("menu-overlay").classList.add('menu-overlay');
        document.getElementById("userList").style.right = "0";
      } else {
        // document.getElementById("menu-overlay").classList.remove('menu-overlay');
        document.getElementById("userList").style.right = "-100%";
      }
    },
    leaveConversation: function() {
      this.$modal.show("dialog", {
        title: "Leave Conversation",
        text: "Do you want to leave this conversation ?",
        buttons: [
          {
            title: "<button class='btn btn-warning'>CONFIRM</button>",
            handler: () => {
              this.$store.dispatch('leaveConversation', {
                uid: this.userInfo.id,
                groupId: this.group.groupId
              }).then(() => {
                this.$modal.hide('dialog')
                this.$router.push('/personal');
                this.current = this.$route.name;
              });
            }
          },
          {
            title: "CANCEL"
          }
        ]
      });
    },
    getType(type) {
			if (type == 1) {
				return "mentioned you.";
			}
			if (type == 2) {
				return "replied your comment.";
			}
			if (type == 3) {
				return "comment in your post.";
			}
    },
    notiRead() {
			document.getElementById("notify").addEventListener("scroll", this.scrollA);
			let a = this;
			// setTimeout(function() {
				a.$store.dispatch(NOTIFY_READ, a.userInfo._id);
			// }, 8000);
    },
    scrollA() {
			let ul = document.getElementById("notify");
			if (Math.floor(ul.offsetHeight + ul.scrollTop) == ul.scrollHeight) {
				let u = this.userInfo;
				u.limit = this.inactiveNotify.length + 10;
				this.$store.dispatch(GET_INACTIVE_NOTIFICATION, u);
			}
    },
    show(i) {
			this.$store.dispatch(GET_POST, i);
			setTimeout(() => {
				this.$modal.show(
					modal,
					{ i: this.post, isMobile: isMobile.any() },
					{
						height: "auto",
						width: isMobile.any() ? "100%" : "80%",
						style: { "z-index": 9000 },
						scrollable: true
					},
					{
						closed: this.hide
					},
					{
						"before-open": this.$forceUpdate(),
						"before-close": this.unsub()
					}
				);
			}, 200);
    },
    hide() {
			this.$modal.hide("view-post");
		},
  },
  destroyed() {
    document.getElementById("notify").removeEventListener("scroll", this.scrollA);
  }
};
</script>

<style>
.nav-link {
  cursor: pointer;
}
.vue-dialog-button .btn {
  height: 100%;
  width: 100%;
}
.navbar {
  box-shadow: none;
}
.notify-count {
	text-align: center;
	box-sizing: border-box;
  font-weight: bold;
	height: 18px;
	width: 18px;
	position: absolute;
	line-height: 13px;
	/* font-size: 0.7em !important; */
  border: 2px solid;
}

.notify-dropdown .dropdown-menu.show ul {
	max-height: 500px;
	overflow-y: auto;
}
.navbar .list-group.list-group-flush::-webkit-scrollbar,
.navbar .list-group.list-group-flush::-webkit-scrollbar-thumb,
.navbar .list-group.list-group-flush::-webkit-scrollbar-track {
	display: none;
}
.navbar .list-group.list-group-flush:hover::-webkit-scrollbar,
.navbar .list-group.list-group-flush:hover::-webkit-scrollbar-thumb,
.navbar .list-group.list-group-flush:hover::-webkit-scrollbar-track {
	display: block;
}
/* .navbar .list-group.list-group-flush:hover::-webkit-scrollbar {
	width: 10px;
} */

.navbar .list-group.list-group-flush:hover::-webkit-scrollbar-thumb {
	background: #666;
	border-radius: 20px;
}

.navbar .list-group.list-group-flush:hover::-webkit-scrollbar-track {
	background: #ddd;
	border-radius: 20px;
}
.notify-chat {
	min-width: 350px;
}
.nav-mobile {
  max-width: 40vw;
  text-overflow: ellipsis;
  overflow: hidden
}
</style>



