/* eslint-disable */
import Vue from 'vue';
import firebase from 'firebase';
import { CREATE_NEW_POST, FETCH_POST_LIST } from './actions.type';
import db, { storageRef } from '../database/firestore';
import query from '../database/db.actions';
import postModel from '../models/post.model';

import { POSTS } from '../database/db.documents';

let state = {
  // user: "admin"
  imgURL: {},
  img: [],
  imageProgress: {}
};
let getters = {};
let mutations = {
  setProgress(state, data) {
    Vue.set(state.imageProgress, data.name, data.progress);
  },
  setUploadImageNewPost(state, data) {
    state.img.push(data);
    Vue.set(state.imgURL, data.id, data.url);
  },
  reset(state) {
    state.imageProgress = {};
    state.imgURL = {};
  }
};
let actions = {
  [CREATE_NEW_POST](c, i) {
    if (i.newPost.title.toString().trim().length) {
      const post = {
        content: i.newPost.content.toString().trim(),
        title: i.newPost.title.toString().trim(),
        owner: i.user.name,
        ownerEmail: i.user.email,
        userId: i.user._id,
        img: i.newPost.img
      };
      const p = new postModel(post);
      query.createPost(
        POSTS,
        p,
        db,
        (d) => {
          console.log(d);
          c.commit("reset")
        },
        e => console.log(e),
      );
    }
  },
  RESET_IMAGE(c) {
    c.commit("reset")
  },
  UPLOAD_POST_IMAGE(c, data) {
    let uploadTask = storageRef.child(data.path).put(data.file);
    uploadTask.on(
      'state_changed',
      (snapshot) => {
        let progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log(`Upload is ${  progress  }% done`);
        c.commit('setProgress', {progress, name: data.file.id})
        switch (snapshot.state) {
          case firebase.storage.TaskState.PAUSED: // or 'paused'
            console.log('Upload is paused');
            break;
          case firebase.storage.TaskState.RUNNING: // or 'running'
            console.log('Upload is running');
            break;
        }
      },
      (error) => {
        console.log(error);
      },
      () => {
        uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
          console.log(downloadURL)
          c.commit('setUploadImageNewPost', {
            url: downloadURL,
            id: data.file.id,
          });
        });
      }
    );
  },
  // delete(c, i) {
  //   db.collection('POSTS').where('title', '==', '').get().then(doc => {
  //     doc.forEach(d => {
  //       db.collection('POSTS').doc(d.id).delete();
  //     })
  //   })
  // }
};

let plugins = {};

export default {
  state,
  getters,
  mutations,
  actions,
  plugins,
};



